.root {
  background: linear-gradient(
    90deg,
    #dc6dfe 0%,
    #dc6dfe 6.25%,
    #dc6cfe 12.5%,
    #db6bfe 18.75%,
    #d968fe 25%,
    #d564fe 31.25%,
    #d05efe 37.5%,
    #c955fe 43.75%,
    #be49fe 50%,
    #af39fd 56.25%,
    #9926fd 62.5%,
    #7c0ffd 68.75%,
    #5902ed 75%,
    #3702cc 81.25%,
    #1802a5 87.5%,
    #020378 93.75%,
    #010d45 100%
  );
  border-radius: 20px;
  position: relative;
  overflow: hidden;

  font-family: "Gilroy";
  color: #010b50;
}
.text_wrapper {
}
.container {
  width: 65%;
  max-width: 450px;
  margin: 20px;

  position: relative;
  z-index: 1;

  @media (max-width: 600px) {
    margin-bottom: 10px;
  }
}
.title {
  font-weight: 900;
  font-size: 39px;
  line-height: 0.9;
  text-transform: uppercase;
  margin: 0;
  margin-bottom: 20px;

  @media (max-width: 600px) {
    font-size: 35px;
    margin-bottom: 10px;
  }
  @media (max-width: 450px) {
    font-size: 25px;
  }
}
.text {
  font-weight: 600;
  font-size: 16px;
  line-height: 120%;

  & p {
    margin: 0;
  }

  & span {
    color: white;
  }
  @media (max-width: 450px) {
    font-size: 13px;
  }
}
.information {
  font-weight: 600;
  font-size: 15px;
  letter-spacing: 0.03em;
  color: white;

  margin: 20px;
  margin-top: 40px;
  display: block;

  cursor: pointer;

  @media (max-width: 600px) {
    margin-top: 10px;
  }

  @media (max-width: 450px) {
    font-size: 12px;
  }
}

.img {
  position: absolute;
  z-index: 0;
  right: 0;
  top: 0;

  height: 100%;
  width: auto;
}
