$color-white: #ffffff;

.root {
  background: linear-gradient(
    86deg,
    #010d45 13.07%,
    #010e47 18.93%,
    #01114e 24.78%,
    #01175a 30.64%,
    #012169 36.49%,
    #012f7c 42.35%,
    #014191 48.21%,
    #0159a9 54.06%,
    #0176c1 59.92%,
    #0197da 65.77%,
    #01bbf1 71.63%,
    #09d9fe 77.48%,
    #1cebff 83.34%,
    #2bf8ff 89.2%,
    #37fffd 95.05%,
    #3efff8 100.91%,
    #40fff7 106.76%
  );
  border-radius: 20px;

  display: flex;
  justify-content: space-between;
  align-items: center;
  overflow: hidden;
}

$imgWidth: 34vw;

.img {
  height: auto;
  width: $imgWidth;
  max-width: 400px;

  @media (max-width: 580px) {
    margin-right: -10vw;
    align-self: flex-end;
  }

  @media (max-width: 380px) {
    right: 0;
    width: 40vw;
  }
}

.container {
  font-family: "Gilroy";
  width: 100%;
  max-width: 700px;
  padding-right: 5px;
  margin: 40px;
  margin-right: 0;

  & * {
    display: block;
  }

  @media (max-width: 1080px) {
    width: calc(100% - #{$imgWidth});
  }
  @media (max-width: 700px) {
    margin: 20px;
    margin-right: 0;
  }
  @media (max-width: 580px) {
    width: calc(100% - #{$imgWidth} + 10vw);
  }
  @media (max-width: 380px) {
    max-width: 235px;
    width: 100%;
  }
}
.title {
  font-weight: 900;
  font-size: 60px;
  letter-spacing: 0.03em;
  color: #fcd517;
  text-transform: uppercase;
  white-space: nowrap;

  margin: 0;
  margin-bottom: 40px;

  @media (max-width: 880px) {
    font-size: 40px;
    margin-bottom: 20px;
  }

  @media (max-width: 580px) {
    font-size: 30px;
    margin-bottom: 15px;
  }

  @media (max-width: 400px) {
    letter-spacing: 0;
  }
}
.text {
  font-weight: 600;
  font-size: 22px;
  letter-spacing: 0.03em;
  color: #ffffff;

  @media (max-width: 880px) {
    font-size: 18px;
  }

  @media (max-width: 580px) {
    font-size: 13px;
  }
}
