.root {
  position: relative;
  overflow: hidden;
  background: linear-gradient(
    93deg,
    #180026 -4.98%,
    #180026 0.97%,
    #180026 6.91%,
    #180026 12.86%,
    #180027 18.81%,
    #190027 24.75%,
    #1a0029 30.7%,
    #1c012b 36.65%,
    #1f022f 42.6%,
    #240235 48.54%,
    #2a043c 54.49%,
    #320547 60.44%,
    #3d0854 66.38%,
    #4b0a66 72.33%,
    #5d0e7c 78.28%,
    #721397 84.22%,
    #8d18b8 90.17%
  );
  border-radius: 13px;
  font-family: "Gilroy";
}
.container {
  margin: 20px;
  margin-bottom: 45px;
  width: fit-content;

  position: relative;
  z-index: 1;
}
.title {
  font-weight: 900;
  font-size: 39px;
  color: #ffffff;
  white-space: nowrap;
  text-transform: uppercase;

  margin: 0;
}
.text {
  font-weight: 600;
  font-size: 14px;
  color: #ffffff;

  margin: 0;
  margin-bottom: 15px;
  max-width: 250px;
}
.information {
  font-weight: 600;
  font-size: 14px;

  color: #d00b98;
  cursor: pointer;

  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 1;
  margin: 20px;
}
.img {
  position: absolute;
  right: -22px;
  bottom: -37px;
  z-index: 0;

  height: 80%;
  width: auto;
}
