@import "sass/index";

.rowBanners {
  $gap: 10px;

  display: flex;
  flex-wrap: wrap;
  gap: $gap;
  margin-top: $gap;

  & > * {
    min-width: 360px;

    &:first-child {
      flex-grow: 2;
    }

    &:last-child {
      flex-grow: 1;
    }

    @media (max-width: 600px) {
      min-width: auto;
    }
  }
}

.bets_wrapper {
  display: flex;
  flex-direction: column;
  margin-top: 50px - spacing(3.5);
  font-size: 12px;
  line-height: 15px;
  letter-spacing: 1px;
  color: #9f8dd0;
  & > span {
    margin-left: 30px;
  }
  @media (max-width: 500px) {
    margin-top: 10px;
  }
}

.title {
  font-family: "Gilroy", sans-serif;
  font-weight: bold;
  margin: spacing(3.5) spacing(2.75);
  width: 136px;
  font-size: 14px;
  letter-spacing: 1px;

  color: use(--secondary-main);
}
